
.vue-simple-date-time-picker {
  position: relative;
  width: auto;
  display: table;
  font-size: 1em;
  font-family: inherit;
}
.vue-simple-date-time-picker input {
  z-index: 1;
}
.dpicker-container {
  z-index: 2;
}
.dpicker-invisible {
  display: none;
}
.dpicker-visible {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 300px;
  background-color: white;
  border: 1px solid #e6e6e6;
  padding: 1em;
}
.vue-simple-date-time-picker.time .dpicker-visible {
  min-width: 100%;
}
.vue-simple-date-time-picker.time .dpicker-container > *:not(.dpicker-time) {
  display: none;
}
.dpicker-month, .dpicker-year {
  -webkit-box-ordinal-group: 2;
      -ms-flex-order: 1;
          order: 1;
}
table {
  -webkit-box-ordinal-group: 3;
      -ms-flex-order: 2;
          order: 2;
}
.dpicker-time {
  -webkit-box-ordinal-group: 4;
      -ms-flex-order: 3;
          order: 3;
}
select {
  margin: 0 1em;
}
.dpicker-inactive {
  color: #e6e6e6;
}
table button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background-color: transparent;
  font-size: inherit;
  font-family: inherit;
  padding: 0.5em 1em;
}
table button:hover {
    background-color: #e6e6e6;
}
table button.dpicker-active {
    font-weight: bold;
    background-color: #ffab40;
}



